<template>
  <div class="operations">
    <el-tabs v-model="operationsSelect" type="card">
      <el-tab-pane label="ServerManagement" name="ServerManagement">
        <div class="operation-container"><server-management></server-management></div>
      </el-tab-pane>
      <el-tab-pane label="Volume Management" name="VolumeManagement">
        <div class="operation-container"> <volume-management></volume-management></div>
      </el-tab-pane>
<!--      <el-tab-pane label="Failure List" name="FailureList">-->
<!--        <div class="operation-container"><failure-list></failure-list></div>-->
<!--      </el-tab-pane>-->
    </el-tabs>
  </div>
</template>

<script>
import ServerManagement from './serverManagement'
import VolumeManagement from './volumeManagement'
import FailureList from './failureList'
export default {
  name: 'Operations',
  components: {
    ServerManagement,
    VolumeManagement,
    FailureList
  },
  data () {
    return {
      operationsSelect: 'ServerManagement'
    }
  },
  methods: {
  }
}
</script>

<style>
  .operation-container{
    width:100%;
    min-height: calc(100vh - 180px);
    padding: 20px;
    box-sizing:border-box;
    background: #fff;
  }
  .operations .el-tabs__nav-wrap{
    margin-bottom: 0;
  }
  .operations .el-tabs__header{
    margin-bottom: 0;
  }
  .operations .el-tabs--card>.el-tabs__header .el-tabs__nav{
    border:none;
  }
  .operations .el-tabs--card>.el-tabs__header{
    border:none;
  }
  .operations .el-tabs--card>.el-tabs__header .el-tabs__item{
    border:none;
  }
  .operations .el-tabs__item{
    background: #E3E8ED;
    color:#333333;
    border:none;
    margin-right: 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  .operations .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
    background: #fff;
    color:#06268D;
  }
</style>



