<template>
  <div class="operation-server">
    <div v-loading="resData.loading">
      <el-table
        :data="resData.resLists"
        style="width: 100%">
        <el-table-column prop="name" label="Cluster">
        </el-table-column>
        <el-table-column prop="allDataNode.zoneName" label="IDC">
        </el-table-column>
        <el-table-column prop="addr" label="Server IP">
        </el-table-column>
        <el-table-column prop="serverType" label="Service Type">
        </el-table-column>
        <el-table-column prop="statusStr" label="Status">
        </el-table-column>
        <el-table-column prop="allDataNode.used" label="Used Capacity">
        </el-table-column>
        <el-table-column prop="allDataNode.total" label="Total Capacity">
        </el-table-column>
        <el-table-column prop="allDataNode.capacityRatio" label="Used %">
        </el-table-column>
        <el-table-column label="Actions" width="230">
          <template slot-scope="scope">
            <el-button type="text" class="text-btn" v-show="scope.row.serverType === 'dataNodes'" @click="openDialog('editDisk', scope.row)">Off-line Disk</el-button>
            <el-button type="text" class="text-btn" @click="editServer(scope.row)">Off-line Server</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="clearfix mt20">
        <el-pagination
          class="fr"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="resData.page.pageSizes"
          :page-size="resData.page.pageSize"
          layout="sizes, prev, pager, next"
          :total="resData.page.totalRecord">
        </el-pagination>
        <span class="fr page-tips pr10">Each page shows</span>
      </div>
    </div>
    <!-- Disk -->
    <el-dialog
      title="Off-line Disk"
      :visible.sync="diskDialog"
      width="30%"
      @close="closeDialog()">
      <p class="mb20">{{diskTip}}</p>
      <el-checkbox-group v-model="checkList">
        <el-checkbox v-for="item in checkListVal" :label="item" :key="item">{{item}}</el-checkbox>
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">No</el-button>
        <el-button type="primary"  @click="editDisk">Yes</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import baseGql from '../../graphql/operations'
export default {
  name: 'OperationServer',
  data () {
    return {
      resData: {
        loading: true,
        page: {
          pageSizes: [10, 20, 30, 40],
          pageNo: 1,
          pageSize: 10,
          totalRecord: 0,
          totalPage: 1
        },
        resLists: []
      },
      serverList: [],
      dataNodesList: [],
      metaNodesList: [],
      diskDialog: false,
      diskData: {},
      checkList: [],
      checkListVal: [],
      diskTip: 'Are you sure you want to off-line the next Disk?'
    }
  },
  methods: {
    handleSizeChange (val) {
      this.resData.page.pageSize = val
      this.resData.page.pageNo = 1
      this.handleCurrentChange(1)
    },
    handleCurrentChange (val) {
      this.resData.page.pageNo = val
      const start = (val - 1) * this.resData.page.pageSize
      const end = val * this.resData.page.pageSize
      this.resData.resLists = this.serverList.slice(start, end)
    },
    queryServerList () {
      const that = this
      that.resData.loading = true
      const variables = {
        num: 10000
      }
      this.apollo.query(this.url.cluster, baseGql.serverManagementList, variables).then((res) => {
        that.resData.loading = false
        if (res) {
          const data = res.data
          this.dataNodesList = data.clusterView.dataNodes
          this.metaNodesList = data.clusterView.metaNodes
          this.dataNodesList.forEach(item => {
            item.name = data.clusterView.name
            item.serverType = 'dataNodes'
            item.statusStr = item.status.toString()
            item.allDataNode = item.toDataNode
            item.allDataNode.capacityRatio = ((item.toDataNode.usageRatio) * 100).toFixed(2) + '%'
            this.checkListVal = item.reportDisks
          })
          this.metaNodesList.forEach(item => {
            item.name = data.clusterView.name
            item.serverType = 'metaNodes'
            item.statusStr = item.status.toString()
            item.allDataNode = item.toMetaNode
            item.allDataNode.capacityRatio = ((item.toMetaNode.ratio) * 100).toFixed(2) + '%'
          })
          this.serverList = this.dataNodesList.concat(this.metaNodesList)
          // 按创建时间倒序排列
          this.serverList.sort(function (a, b) {
            if (a.status === b.status) {
              return 0
            } else {
              return b.status ? -1 : 1
            }
          })
          this.resData.page.totalRecord = this.serverList.length
          this.handleCurrentChange(1)
        } else {
        }
      }).catch((error) => {
        that.resData.loading = false
        console.log(error)
      })
    },
    openDialog (tag, row) {
      if (tag === 'editDisk') {
        this.diskDialog = true
        this.diskData = Object.assign({}, row)
        if (this.checkListVal.length === 0) {
          this.diskTip = 'No disk!'
        }
      }
    },
    editDisk () {
      if (this.checkList.length === 0) {
        this.$message.error('No disk!')
      }
      for (let i = 0; i < this.checkList.length; i++) {
        const variables = {
          offLineAddr: this.diskData.addr,
          diskPath: this.checkList[i]
        }
        this.apollo.mutation(this.url.cluster, baseGql.decommissionDisk, variables).then((res) => {
          if (res.code === 200) {
            this.queryServerList()
            this.diskDialog = false
            this.$message({
              message: 'success',
              type: 'success'
            })
          } else {
            this.$message.error(res.message)
          }
        })
      }
    },
    editServer (row) {
      const variables = {
        offLineAddr: row.addr
      }
      this.$confirm('Are you sure you want to off-line the Server?', 'Off-line Server', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'No',
        type: 'warning'
      }).then(() => {
        if (row.serverType === 'dataNodes') {
          this.apollo.mutation(this.url.cluster, baseGql.decommissionDataNode, variables).then((res) => {
            if (res.code === 200) {
              this.queryServerList()
              this.$message({
                message: res.data.decommissionDataNode.message,
                type: 'success'
              })
            } else {
              this.$message.error(res.message)
            }
          })
        } else if (row.serverType === 'metaNodes') {
          this.apollo.mutation(this.url.cluster, baseGql.decommissionMetaNode, variables).then((res) => {
            if (res.code === 200) {
              this.queryServerList()
              this.$message({
                message: res.data.decommissionDataNode.message,
                type: 'success'
              })
            } else {
              this.$message.error(res.message)
            }
          })
        }
      }).catch(() => {
      })
    },
    closeDialog () {
      this.diskDialog = false
      this.checkList = []
    }
  },
  mounted () {
    this.queryServerList()
  }
}
</script>

<style scoped>

</style>



